import Image from "next/image";
import Link from "next/link";
import { notFound } from "next/navigation";

import { subcategories, products, productVariants, productImages } from "@/data/store";

function money(n: number) {
  return `‚Çπ${Number(n).toFixed(0)}`;
}

function getPrimaryImageUrl(productId: number) {
  const primary = productImages.find((img) => img.product_id === productId && img.is_primary);
  const any = productImages.find((img) => img.product_id === productId);
  return primary?.url || any?.url || "/example.png";
}

function getBestPrice(productId: number, basePrice?: number) {
  const vars = productVariants.filter((v) => v.product_id === productId);
  if (vars.length === 0) {
    const p = basePrice ?? 0;
    return { price: p, mrp: null as number | null, label: "" };
  }
  // pick cheapest variant for listing
  const best = vars.reduce((min, v) => (v.price < min.price ? v : min), vars[0]);
  return { price: best.price, mrp: best.mrp ?? null, label: best.label ?? "" };
}

export default function SubcategoryPage({
  params,
}: {
  params: { slug: string };
}) {
  const { slug } = params;

  const currentSub = subcategories.find((s) => s.slug === slug);
  if (!currentSub) return notFound();

  // left rail (siblings in same category)
  const leftList = subcategories.filter((s) => s.category_id === currentSub.category_id);

  // products inside this subcategory (your store uses subcategory_id)
  const list = products.filter((p) => p.subcategory_id === currentSub.id);

  return (
    <main className="min-h-screen bg-gray-50 pb-16">
      {/* TOP SEARCH HEADER */}
      <div className="bg-blue-700 px-4 py-3 flex items-center gap-3 text-white">
        <div className="flex-1 rounded-md bg-blue-600/40 px-3 py-2 text-sm">
          üîé Search JioMart
        </div>

        <Link href="/cart" className="relative text-lg">
          üõí
          <span className="absolute -top-2 -right-2 bg-red-500 text-[10px] px-1 rounded-full">
            8
          </span>
        </Link>
      </div>

      {/* LOCATION */}
      <div className="bg-white px-4 py-2 text-sm border-b">üìç Mumbai 400020</div>

      {/* SORT / FILTER BAR */}
      <div className="bg-white px-4 py-3 flex justify-between text-sm border-b">
        <button className="flex items-center gap-2">‚Üï Sort</button>
        <button className="flex items-center gap-2">‚öô Filter</button>
      </div>

      {/* BODY: LEFT RAIL + GRID */}
      <section className="grid grid-cols-[92px_1fr]">
        {/* LEFT RAIL */}
        <aside className="bg-white border-r min-h-[calc(100vh-160px)]">
          <div className="py-2">
            {leftList.map((s) => {
              const active = s.slug === currentSub.slug;
              return (
                <Link
                  key={s.id}
                  href={`/subcategory/${s.slug}`}
                  className={`block px-2 py-3 text-[11px] leading-tight border-l-4 ${
                    active
                      ? "border-blue-600 bg-blue-50 text-blue-700 font-medium"
                      : "border-transparent text-gray-700"
                  }`}
                >
                  <div className="w-10 h-10 bg-gray-100 rounded-md mb-2 mx-auto overflow-hidden flex items-center justify-center">
                    <Image src={s.img} alt={s.name} width={40} height={40} className="object-contain" />
                  </div>
                  <div className="text-center">{s.name}</div>
                </Link>
              );
            })}
          </div>
        </aside>

        {/* RIGHT */}
        <div className="p-3">
          <div className="text-sm font-semibold mb-2">Search Results</div>

          <div className="flex gap-2 mb-3 overflow-x-auto">
            <button className="px-3 py-1 rounded-full bg-gray-100 text-xs whitespace-nowrap">
              ‚Üï By Popularity
            </button>
            <button className="px-3 py-1 rounded-full bg-gray-100 text-xs whitespace-nowrap">
              Brands
            </button>
            <button className="px-3 py-1 rounded-full bg-gray-100 text-xs whitespace-nowrap">
              Filters
            </button>
          </div>

          <div className="grid grid-cols-2 gap-3">
            {list.map((p) => {
              const imgUrl = getPrimaryImageUrl(p.id);
              const { price, mrp, label } = getBestPrice(p.id, p.base_price);

              const offPct =
                mrp && price ? Math.round(((mrp - price) / mrp) * 100) : null;

              return (
                <div key={p.id} className="bg-white rounded-lg p-2 shadow-sm">
                  <div className="flex justify-end text-gray-400">‚ô°</div>

                  <Link href={`/product/${p.slug}`} className="block">
                    <Image
                      src={imgUrl}
                      alt={p.name}
                      width={180}
                      height={180}
                      className="mx-auto object-contain"
                    />
                  </Link>

                  <p className="text-xs text-gray-500 mt-1">{label}</p>

                  <p className="text-sm font-medium leading-tight line-clamp-2">
                    {p.name}
                  </p>

                  <div className="mt-1">
                    <span className="font-semibold">{money(price)}</span>
                    {mrp ? (
                      <span className="text-xs line-through text-gray-400 ml-1">
                        {money(mrp)}
                      </span>
                    ) : null}
                  </div>

                  {offPct ? (
                    <p className="text-red-600 text-xs font-medium">{offPct}% Off</p>
                  ) : null}

                  <button className="mt-2 w-full border border-blue-600 text-blue-600 rounded-md py-1 text-sm font-medium">
                    Add +
                  </button>

                  <div className="text-center text-green-600 text-xs mt-1">‚ö° Quick</div>
                </div>
              );
            })}

            {list.length === 0 && (
              <div className="col-span-2 bg-white rounded-lg p-4 text-sm text-gray-600">
                No products found for this subcategory.
              </div>
            )}
          </div>
        </div>
      </section>

      {/* BOTTOM NAV */}
      <nav className="fixed bottom-0 left-0 right-0 bg-white border-t flex justify-around py-2 text-xs">
        <Link href="/">üè† Home</Link>
        <Link href="/categories">üìÇ Categories</Link>
        <Link href="/wishlist">‚ù§Ô∏è Wishlist</Link>
        <Link href="/orders">üì¶ Orders</Link>
      </nav>
    </main>
  );
}
